body{
  margin:0;
  box-sizing: border-box;
  background-color: #eee;
}

img{
  width:180px;
  cursor: pointer;
}

header{
  background-color: #0B1C23;
  padding:8px;
}

.top-nav{
  display:grid;
  grid-template-columns: 1fr auto 1fr;
}

.midt-nav {
  display: flex;
  place-items: center;
}

.midt-nav a {
  margin-right: 60px; 
}

.midt-nav a:last-child{
  margin-right: 0; 
}

.top-nav a{
  text-decoration: none;
  color:	white;
  font-weight: 600;
  font-size:15px;
}

.favoritt-ikon{
  color:white;
}