.alfabetisk {
    display: grid;
    grid-auto-flow: column;
    padding: 10px;
    justify-content: center;
    text-align: center;
    gap: 20px;
    list-style: none;
    margin-top: 34px;
    margin-bottom: 36px;
}

.alfabetisk li {
    cursor: pointer;
}

.filmoversikt {
    align-items:center;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(2,495px);
    gap: 28px;
}

.nav-div{
    display:grid;
    grid-template-columns: 30px 48px auto;
}

.nav-div p{
    margin-top:6px;
    text-align: center;
}

.nav-ikon{
    width:30px;
    height:30px;
    cursor: pointer;
}

.h1favourites{
    text-align: center;
    margin-top: 40px;
    margin-bottom:50px;
}

.filmoversikt-checkbox{
    display: grid;
    grid-template-rows: 10px auto;
    grid-template-columns: 90px auto;
}

.genres{
    padding:20px;
    width:120px;
    margin-top: -50px;
    margin-left:15px;
}


.genres-h3{
    margin-left: 12px;
    margin-bottom:0px;
}

.genres-form{
    display: grid;
    grid-template-columns: repeat(1,100px);
    padding:10px;
}

form{
    line-height: 28px;
    display: flex;
    align-items:center;
}

#checkbox{
    margin-right: 10px;
}

.inpSearch{
    position:absolute;
    width:0px;
    top:15px;
    left:1165px;
    padding: 2.5px;
    border-radius:2px;
    animation: snurr 0.5s forwards linear;
  }

  @keyframes snurr{
    to{
        width:200px;
    }
}

.search{
    cursor: pointer;
    height:23px;
    width:23px;
    color:white;
    position:absolute;
    top:15px;
    left:1380px;
}

.searchHide{
    display:none;
}

.noResults{
    display:flex;
    justify-content:center;
    align-items:center;
}

.test{
    display:none;
}