.filmdiv {
    height: 318px;
    width: 495px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 2px 8px rgb(199, 199, 199);
    margin-bottom: 20px;
    color: rgb(0, 0, 0);
    display: grid;
    grid-template-columns: 190px 60px auto;
    gap: 14px;
    border-radius: 2px;
}

.left img {
    height: 318px;
    width: 185px;
    cursor: auto;
}

.middle {
    line-height: 0px;
    margin-top: 15px;
    text-align: center;
    position: relative;
}

.stjerne {
    width: 35px;
    cursor: auto;
}

.rating {
    margin-top: 12px;
}

.avTi {
    font-size: 11px;
}

.bottom {
    position: absolute;
    width: 285px;
    /*height: 135px;*/
    line-height: 20px;
    overflow: hidden;
}

.storyline {
    font-size: 14px;
    font-family: sans-serif;
    margin-top: 12px;
    text-align: start;
}

.year {
    font-size: 14px;
    margin-top: -9px;
}


 .right{
     display:grid;
     grid-template-rows: auto auto;
     gap:150px;
 }


 .sirkel-div{
    position: relative;
    margin-left: 150px;
    top:8px;
    width: 0px;
    cursor: pointer;
 }


 .hjerte{
    position: absolute;
    top:4px;
    left:3px;
    width:17px;
    height: 17px;
 }

 .sirkel{
    border:1px solid black;
    border-radius: 50%;
    width:22px;
    height:22px;
}

.actors{
    margin-top:156px;
    font-size: 14px;
    width:295px;
    text-align: start;
}

.actors h4{
   font-weight: 550;
}


.stars{
    text-align: start;
}

.propsSpan{
    font-weight: 400;
}

.genre{
    margin-top: 50px;
}
